import React, { useState, useEffect, useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import { LineChart, Line, BarChart, CartesianGrid, Bar, XAxis, YAxis, Tooltip, Legend, ResponsiveContainer, Cell } from 'recharts';
import { format } from 'date-fns';

// Stats Card Component
const StatsCard = ({ title, value, icon, bgColor = 'bg-primary', textColor = 'text-white' }) => (
  <div className="col-xl-3 col-md-6">
    <div className={`card ${bgColor} ${textColor} mb-3`}>
      <div className="card-body">
        <div className="d-flex align-items-center">
          <div className="flex-grow-1">
            <h6 className="mb-3">{title}</h6>
            <h3 className="mb-0">{value}</h3>
          </div>
          {icon && <div className="flex-shrink-0">{icon}</div>}
        </div>
      </div>
    </div>
  </div>
);

// Areas of Improvement Component
const AreasOfImprovement = ({ areas }) => {
  if (!areas?.length) return null;

  return (
    <div className="mb-4">
      <h5 className="mb-3">Areas Needing Focus</h5>
      <ul className="list-group">
        {areas.map((area, index) => (
          <li key={index} className="list-group-item">
            <i className="bi bi-exclamation-triangle text-warning me-2"></i>
            {area}
          </li>
        ))}
      </ul>
    </div>
  );
};

// Agent Performance Chart
const AgentPerformanceChart = ({ data }) => {
  if (!data?.length) return null;

  return (
    <div className="card mb-4">
      <div className="card-header bg-white">
        <h5 className="card-title mb-0">Agent Performance</h5>
      </div>
      <div className="card-body">
        <div style={{ width: '100%', height: 400 }}>
          <ResponsiveContainer>
            <BarChart
              layout="vertical"
              data={data}
              margin={{ top: 20, right: 30, left: 100, bottom: 5 }}
            >
              <XAxis type="number" domain={[0, 100]} />
              <YAxis dataKey="name" type="category" />
              <Tooltip />
              <Legend />
              <Bar dataKey="averageScore" fill="#4f46e5" name="Score" />
            </BarChart>
          </ResponsiveContainer>
        </div>
      </div>
    </div>
  );
};

// Parameter Score Card Component
const ParameterScoreCard = ({ parameter, averageScore, count, coverage }) => (
  <div className="col-md-3 mb-3">
    <div className="card h-100">
      <div className="card-body">
        <h6 className="card-subtitle mb-2 text-muted text-truncate" title={parameter}>
          {parameter}
        </h6>
        <div className="d-flex justify-content-between align-items-end mt-3">
          <div>
            <h3 className="mb-0" style={{ color: averageScore >= 80 ? '#10B981' : averageScore >= 60 ? '#F59E0B' : '#EF4444' }}>
              {averageScore}%
            </h3>
            <small className="text-muted">
              {count} evaluations ({coverage}% coverage)
            </small>
          </div>
          <div className={`badge ${
            averageScore >= 80 ? 'bg-success' : 
            averageScore >= 60 ? 'bg-warning' : 
            'bg-danger'
          }`}>
            {averageScore >= 80 ? 'Good' : 
             averageScore >= 60 ? 'Average' : 
             'Needs Improvement'}
          </div>
        </div>
      </div>
    </div>
  </div>
);

// Parameter Bar Chart Component
const ParameterBarChart = ({ data, parameters, dateRange }) => {
  // Check if there are parameters to display
  if (!parameters || Object.keys(parameters).length === 0) {
    return null;
  }

  // Transform parameters data for chart display
  const transformedData = Object.entries(parameters).map(([param, data]) => ({
    name: param,
    score: data.score * 20, // Scale to percentage (0-5 to 0-100)
    color: data.score * 20 >= 80 ? '#10B981' : 
           data.score * 20 >= 60 ? '#F59E0B' : '#EF4444'
  }));

  const CustomTooltip = ({ active, payload }) => {
    if (active && payload && payload.length) {
      const data = payload[0].payload;
      return (
        <div className="bg-white p-3 border rounded shadow-sm">
          <p className="mb-1"><strong>{data.name}</strong></p>
          <p className="mb-0" style={{ color: data.color }}>
            Score: {data.score}%
          </p>
        </div>
      );
    }
    return null;
  };

  return (
    <div className="card mb-4">
      <div className="card-header bg-white d-flex justify-content-between align-items-center">
        <h5 className="card-title mb-0">Parameter Score Analysis</h5>
        <div className="text-muted small">
          {dateRange?.start} to {dateRange?.end}
        </div>
      </div>
      <div className="card-body">
        <div style={{ width: '100%', height: 500 }}>
          <ResponsiveContainer>
            <BarChart
              data={transformedData}
              margin={{ top: 20, right: 30, left: 40, bottom: 100 }}
              barSize={40}
            >
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis 
                dataKey="name"
                angle={-45}
                textAnchor="end"
                height={100}
                interval={0}
              />
              <YAxis 
                domain={[0, 100]}
                label={{ 
                  value: 'Score (%)', 
                  angle: -90, 
                  position: 'insideLeft',
                  offset: -30
                }}
              />
              <Tooltip content={<CustomTooltip />} />
              <Bar dataKey="score">
                {transformedData.map((entry, index) => (
                  <Cell key={`cell-${index}`} fill={entry.color} />
                ))}
              </Bar>
            </BarChart>
          </ResponsiveContainer>
        </div>
        <div className="mt-3">
          <div className="d-flex justify-content-center gap-4">
            <div className="d-flex align-items-center">
              <div className="badge bg-success me-2">≥ 80%</div>
              <span>Good</span>
            </div>
            <div className="d-flex align-items-center">
              <div className="badge bg-warning me-2">60-79%</div>
              <span>Average</span>
            </div>
            <div className="d-flex align-items-center">
              <div className="badge bg-danger me-2">&lt; 60%</div>
              <span>Needs Improvement</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

const Dashboard = () => {
  const navigate = useNavigate();
  const [metrics, setMetrics] = useState(null);
  const [filters, setFilters] = useState(null);
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 10;
  const [selectedFilters, setSelectedFilters] = useState({
    agentId: '',
    queueId: '',
    startDate: format(new Date(), 'yyyy-MM-dd'),
    endDate: format(new Date(), 'yyyy-MM-dd'),
  });
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  const Pagination = ({ totalItems, itemsPerPage, currentPage, onPageChange }) => {
    const totalPages = Math.ceil(totalItems / itemsPerPage);
    
    if (totalPages <= 1) return null;
  
    return (
      <nav aria-label="Evaluation navigation">
        <ul className="pagination justify-content-end mb-0">
          <li className={`page-item ${currentPage === 1 ? 'disabled' : ''}`}>
            <button
              className="page-link"
              onClick={() => onPageChange(currentPage - 1)}
              disabled={currentPage === 1}
            >
              Previous
            </button>
          </li>
          
          {[...Array(totalPages)].map((_, index) => (
            <li key={index + 1} className={`page-item ${currentPage === index + 1 ? 'active' : ''}`}>
              <button
                className="page-link"
                onClick={() => onPageChange(index + 1)}
              >
                {index + 1}
              </button>
            </li>
          ))}
  
          <li className={`page-item ${currentPage === totalPages ? 'disabled' : ''}`}>
            <button
              className="page-link"
              onClick={() => onPageChange(currentPage + 1)}
              disabled={currentPage === totalPages}
            >
              Next
            </button>
          </li>
        </ul>
      </nav>
    );
  };
  
  const fetchData = useCallback(async () => {
    try {
      setLoading(true);
      setError(null);
  
      const [filtersResponse, metricsResponse] = await Promise.all([
        fetch('/api/dashboard/filters', {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`
          }
        }),
        fetch(`/api/dashboard/metrics?${new URLSearchParams(selectedFilters)}`, {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`
          }
        })
      ]);
  
      if (!filtersResponse.ok || !metricsResponse.ok) {
        throw new Error('Failed to fetch dashboard data');
      }
  
      const [filtersData, metricsData] = await Promise.all([
        filtersResponse.json(),
        metricsResponse.json()
      ]);
  
      console.log('Raw Metrics Data:', metricsData);
      console.log('QA Data Available:', !!metricsData?.qa);
      console.log('Recent Evaluations:', metricsData?.qa?.recentEvaluations?.length || 0);
  
      setFilters(filtersData);
      setMetrics(metricsData);
    } catch (err) {
      console.error('Dashboard error:', err);
      setError(err.message);
    } finally {
      setLoading(false);
    }
  }, [selectedFilters]);

  useEffect(() => {
    fetchData();
  }, [fetchData]);

  const handleFilterChange = (e) => {
    const { name, value } = e.target;
    setSelectedFilters(prev => ({
      ...prev,
      [name]: value
    }));
  };

  if (loading) {
    return (
      <div className="d-flex justify-content-center align-items-center" style={{ height: '80vh' }}>
        <div className="spinner-border text-primary" role="status">
          <span className="visually-hidden">Loading...</span>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="text-center my-5">
        <div className="alert alert-danger">
          <h4>Error Loading Dashboard</h4>
          <p>{error}</p>
          <button 
            className="btn btn-outline-danger"
            onClick={fetchData}
          >
            Retry
          </button>
        </div>
      </div>
    );
  }

  // Find first evaluation to inspect structure
  const sampleEvaluation = metrics?.qa?.recentEvaluations?.[0];
  if (sampleEvaluation) {
    console.log('Sample Evaluation Structure:', {
      hasAgent: !!sampleEvaluation.agent,
      hasAgentName: sampleEvaluation.agent?.name,
      hasEvaluation: !!sampleEvaluation.evaluation,
      hasScores: !!sampleEvaluation.evaluation?.scores,
      hasOverallScore: sampleEvaluation.evaluation?.scores?.overall?.average,
      hasSummary: !!sampleEvaluation.evaluation?.summary,
      hasInteractionData: !!sampleEvaluation.interactionData,
    });
  }

  // Get the overall average score
  const overallScore = metrics?.qa?.totalScore || 0;

  // Get the customer sentiment
  const customerSentiment = Array.isArray(metrics?.qa?.customerSentiment) 
    ? metrics.qa.customerSentiment[0] 
    : (metrics?.qa?.customerSentiment || 'N/A');

  // Check if evaluations exist
  const hasEvaluations = metrics?.qa?.recentEvaluations?.length > 0;

  // Get a sample evaluation to extract parameters data
  const parameters = hasEvaluations ? 
    metrics.qa.recentEvaluations[0]?.evaluation?.scores?.categories || {} : {};

  return (
    <div className="container-fluid py-4">
      {/* Filters */}
      <div className="card mb-4">
        <div className="card-body">
          <h5 className="card-title mb-3">Filters</h5>
          <div className="row g-3">
            <div className="col-md-3">
              <select 
                className="form-select"
                name="agentId"
                value={selectedFilters.agentId}
                onChange={handleFilterChange}
              >
                <option value="">All Agents</option>
                {filters?.agents?.map(agent => (
                  <option key={agent.id} value={agent.id}>
                    {agent.name}
                  </option>
                ))}
              </select>
            </div>

            <div className="col-md-3">
              <select
                className="form-select"
                name="queueId"
                value={selectedFilters.queueId}
                onChange={handleFilterChange}
              >
                <option value="">All Queues</option>
                {filters?.queues?.map(queue => (
                  <option key={queue.id} value={queue.id}>
                    {queue.name}
                  </option>
                ))}
              </select>
            </div>

            <div className="col-md-3">
              <input
                type="date"
                className="form-control"
                name="startDate"
                value={selectedFilters.startDate}
                onChange={handleFilterChange}
              />
            </div>

            <div className="col-md-3">
              <input
                type="date"
                className="form-control"
                name="endDate"
                value={selectedFilters.endDate}
                onChange={handleFilterChange}
              />
            </div>
          </div>
        </div>
      </div>

      {/* Summary Stats */}
      <div className="row mb-4">
        <StatsCard
          title="Total Evaluations"
          value={metrics?.totalEvaluations || 0}
          icon={<i className="bi bi-file-earmark-text fs-4"></i>}
        />
        <StatsCard
          title="Average Score"
          value={`${overallScore}`}
          icon={<i className="bi bi-graph-up fs-4"></i>}
          bgColor="bg-success"
        />
        <StatsCard
          title="Best Performer"
          value={metrics?.qa?.bestPerformer?.name || 'N/A'}
          icon={<i className="bi bi-trophy fs-4"></i>}
          bgColor="bg-info"
        />
        <StatsCard
          title="Poor Performer"
          value={metrics?.qa?.poorPerformer?.name || 'N/A'}
          subtitle={metrics?.qa?.poorPerformer ? `Score: ${metrics.qa.poorPerformer.averageScore}%` : null}
          icon={<i className="bi bi-exclamation-circle fs-4"></i>}
          bgColor="bg-danger"
        />
        <StatsCard
          title="Customer Sentiment"
          value={customerSentiment}
          subtitle={`Based on ${metrics?.totalEvaluations || 0} evaluations`}
          icon={<i className="bi bi-emoji-smile fs-4"></i>}
          bgColor="bg-secondary"
        />
      </div>

      {/* Parameter Analysis */}
      {Object.keys(parameters).length > 0 && (
        <>
          <h5 className="mb-3">Evaluation Parameters</h5>
          <div className="row">
            {Object.entries(parameters).map(([param, data], index) => {
              const excludeParams = ["TotalScore", "Total Score", "Score each parameter from 0 to 5"];
              
              if (excludeParams.includes(param)) return null;
              
              return (
                <ParameterScoreCard
                  key={index}
                  parameter={param}
                  averageScore={data.score * 20} // Scale to percentage (0-5 to 0-100)
                  count={metrics.totalEvaluations}
                  coverage={100} // All evals use the same form
                />
              );
            })}
          </div>
          
          {/* Bar Chart */}
          <ParameterBarChart 
            data={metrics.qa}
            parameters={parameters}
            dateRange={{
              start: selectedFilters.startDate,
              end: selectedFilters.endDate
            }}
          />
        </>
      )}

      {/* Areas of Improvement */}
      <AreasOfImprovement areas={metrics?.qa?.areasNeedingFocus} />

      {/* Agent Performance Chart */}
      <AgentPerformanceChart data={metrics?.qa?.agentPerformance} />

      {/* Recent Evaluations */}
      {metrics?.qa?.recentEvaluations?.length > 0 && (
        <div className="card">
          <div className="card-header bg-white d-flex justify-content-between align-items-center">
            <h5 className="card-title mb-0">Recent Evaluations</h5>
            <div className="text-muted small">
              Showing {Math.min(itemsPerPage * currentPage, metrics.qa.recentEvaluations.length)} of {metrics.qa.recentEvaluations.length}
            </div>
          </div>
          <div className="table-responsive">
            <table className="table table-hover align-middle mb-0">
              <thead className="table-light">
                <tr>
                  <th>Agent</th>
                  <th>Score</th>
                  <th>Date</th>
                  <th>Summary</th>
                  <th>Actions</th>
                </tr>
              </thead>
              <tbody>
                {metrics.qa.recentEvaluations
                  .slice((currentPage - 1) * itemsPerPage, currentPage * itemsPerPage)
                  .map(evaluation => (
                    <tr key={evaluation.id}>
                      <td>
                        <div className="d-flex align-items-center">
                          <div className="avatar-xs me-2">
                            <span className="avatar-title rounded-circle bg-primary bg-soft text-primary">
                              {evaluation.agent?.name?.charAt(0) || 'A'}
                            </span>
                          </div>
                          <span>{evaluation.agent?.name || 'Unknown'}</span>
                        </div>
                      </td>
                      <td>
                        <div className={`badge bg-${
                          evaluation.evaluation?.scores?.overall?.average >= 90 ? 'success' : 
                          evaluation.evaluation?.scores?.overall?.average >= 70 ? 'warning' : 'danger'
                        }`}>
                          {evaluation.evaluation?.scores?.overall?.average || 0}
                        </div>
                      </td>
                      <td>{format(new Date(evaluation.createdAt), 'MMM d, yyyy')}</td>
                      <td>
                        <span className="text-truncate d-inline-block" style={{ maxWidth: '200px' }}>
                          {evaluation.evaluation?.summary || 'No summary available'}
                        </span>
                      </td>
                      <td>
                        <button
                          onClick={() => navigate(`/evaluation/${evaluation.id}`)}
                          className="btn btn-sm btn-outline-primary"
                        >
                          <i className="bi bi-eye me-1"></i>
                          View Details
                        </button>
                      </td>
                    </tr>
                  ))}
              </tbody>
            </table>
          </div>
          <div className="card-footer bg-white">
            <Pagination
              totalItems={metrics.qa.recentEvaluations.length}
              itemsPerPage={itemsPerPage}
              currentPage={currentPage}
              onPageChange={setCurrentPage}
            />
          </div>
        </div>
      )}
    </div>
  );
};

export { ParameterScoreCard, ParameterBarChart };
export default Dashboard;